<template>
	<div>
		<mt-header title="">
			<router-link to="" slot="left"><mt-button icon="back" @click.native="$router.back(-1)"></mt-button></router-link>
		</mt-header>
		<div id="songbg" @click="change()">
			<audio src="./src/common/audio/notebook.mp3" id="music" controls="controls" preload hidden></audio>
			<img id="bgimg" src="../../assets/img/songslist/T002R300x300M000002ugSOe1kQxlB.jpg" />
			<transition enter-actve-class="animated fadeIn" leave-active-class="animated fadeOut">
				<img v-bind:class="roundp" v-if="show" id="roundimg" src="../../assets/img/songslist/T002R300x300M000002ugSOe1kQxlB.jpg" />
			</transition>
			<div id="lyric" v-if="lyric" >
				<ul v-for="(item,index) in list">
					<li>{{item.title}}</li>
				</ul>
			</div>
		</div>
		<div id="iconp">
			<svg class="icon icona" aria-hidden="true" @click="play()">
				<use xlink:href="#icon-bofang" v-show="iconplay"></use>
				<use xlink:href="#icon-plus-pause" v-show="!iconplay"></use>
			</svg>
			<svg class="icon icona" aria-hidden="true"><use xlink:href="#icon-shoucang"></use></svg>
		</div>
		<div id="downloads"><span></span></div>
		<p id="songa"></p>
		<router-link :to="'singerdetails'">
			<mt-cell title=" " is-link><img class="cellimg" slot="icon" src="../../assets/img/songslist/T001R150x150M000000ynT3J1Hrdp1.jpg" /></mt-cell>
		</router-link>
		<router-link :to="'album'">
			<mt-cell title=" Grace()" is-link><img class="cellimg" slot="icon" src="../../assets/img/songslist/T002R300x300M000002ugSOe1kQxlB.jpg" /></mt-cell>
		</router-link>
		<p id="songa"></p>
		<mt-cell title=" 2018-09-09 18:24">
			<img class="cellimg1" slot="icon" src="../../assets/img/songslist/g.jpg" />
			<span>
				12
				<svg class="icon iconzan" aria-hidden="true"><use xlink:href="#icon-zan"></use></svg>
			</span>
		</mt-cell>
		<p id="comment"></p>
		<div id="more">
			<span>
				
				<svg class="icon incom" aria-hidden="true"><use xlink:href="#icon-gengduo"></use></svg>
			</span>
		</div>
		<div id="morem"><span> QQ</span></div>
		<router-view></router-view>
	</div>
</template>

<script>
export default {
	data: function() {
		return {
			iconplay: true,
			roundp: '',
			show: true,
			lyric: false,
			list:[
				{
					title:''
				},
				{
					title:''
				},
				{
					title:''
				},
				{
					title:''
				},
				{
					title:''
				},
				{
					title:''
				},
				{
					title:''
				},
				{
					title:''
				}
			]
		};
	},
	methods: {
		play() {
			//iconplay
			//
			//iconplay
			this.iconplay = !this.iconplay;
			if (this.roundp == 'Rotation') {
				this.roundp = '';
				music.pause();
			} else {
				this.roundp = 'Rotation';
				music.play();
				//0
				// music.currentTime=0 
			}
		},
		change() {
			this.show = !this.show;
			this.lyric = !this.lyric;
		}
	}
};
</script>

<style>
a {
	text-decoration: none;
}
.mint-header {
	background-color: #22d59c;
	height: 45px;
	font-size: 16px;
}
#songbg {
	width: 100%;
}
#bgimg {
	width: 100%;
	opacity: 0.06;
}
#roundimg {
	position: absolute;
	top: 15%;
	left: 15%;
	width: 70%;
	border-radius: 50%;
}
#iconp {
	text-align: center;
	/* background-color: antiquewhite; */
	font-size: 35px;
	letter-spacing: 20px;
	margin: 15px 0;
}
#downloads {
	margin: 20px auto;
	width: 60%;
	height: 40px;
	line-height: 40px;
	border-radius: 32px;
	/* box-shadow: 0 6px 32px rgba(24, 213, 156, 0.5); */
	background-color: #22d59c;
	color: white;
	font-size: 18px;
	text-align: center;
	/* border: 1px solid lightgrey; */
}
#more {
	margin: 20px auto;
	width: 60%;
	height: 40px;
	line-height: 40px;
	border-radius: 32px;
	/* box-shadow: 0 6px 32px rgba(24, 213, 156, 0.5); */
	/* background-color: #22d59c; */
	color: darkgrey;
	font-size: 14px;
	text-align: center;
	border: 1px solid lightgrey;
}
#songa {
	text-align: center;
	margin-bottom: 20px;
}
.cellimg {
	width: 20%;
	margin: 10px;
}
.mint-cell-wrapper {
	font-size: 14px;
	color: gray;
}
.cellimg1 {
	width: 10%;
}
#comment {
	margin-left: 15%;
	color: gray;
}
.iconzan {
	font-size: 28px;
}
.incom {
	color: darkgrey;
}
#morem {
	margin: 35px auto;
	width: 75%;
	height: 40px;
	line-height: 40px;
	border-radius: 32px;
	/* box-shadow: 0 6px 32px rgba(24, 213, 156, 0.5); */
	background-color: #22d59c;
	color: white;
	font-size: 18px;
	text-align: center;
	/* border: 1px solid lightgrey; */
}
#lyric {
	position: absolute;
	top: 12%;
	/* height: 40%; */
	width: 100%;
	text-align: center;
	/* background-color: antiquewhite; */
	line-height: 40px;
	color: gray;
}
@-webkit-keyframes rotation {
	from {
		-webkit-transform: rotate(0deg);
	}

	to {
		-webkit-transform: rotate(360deg);
	}
}

.Rotation {
	-webkit-transform: rotate(360deg);
	animation: rotation 15s linear infinite;
	-moz-animation: rotation 15s linear infinite;
	-webkit-animation: rotation 15s linear infinite;
	-o-animation: rotation 15s linear infinite;
}
</style>



